@import 'reset';
@import 'colors';
@import 'mixins';
@import 'base';

//header
.header {
	display: flex;
	height: 12vh;
}

//Sections

//Section 1 PRODUCT NAME

.product {
	background-color: $second-color;

	&__info {
		display: flex;
		flex-direction: column;

		gap: 0.5em;
		padding: 10px;

		input {
			width: 25em;
			height: 3em;
			font-size: 0.6rem;
			padding-left: 1em;

			&:focus {
				// outline: 1px solid $black-color;
			}
		}
	}

	&__btn__list {
		display: flex;
		justify-content: center;
		gap: 1em;
	}
}
